import { useHistory } from "react-router-dom"
import { useRef, useState } from "react";
import { useSelector, useDispatch } from "react-redux"
import { addWordFB, createWord } from "../redux/modules/word";
import styled from "styled-components"



const Addword = () => {

    const history = useHistory();

    const word_ref = useRef(null);
    const mean_ref = useRef(null);
    const ex_ref = useRef(null);

    const [word_is_empty, setWord_is_empty] = useState(false);
    const [mean_is_empty, setMean_is_empty] = useState(false);
    const [ex_is_empty, setEx_is_empty] = useState(false);

    const dispatch = useDispatch();


    const addWordList = () => {
        dispatch(addWordFB({ word: word_ref.current.value, mean: mean_ref.current.value, ex: ex_ref.current.value }))
    }


    return (

        <div>

            <h3> </h3>

            <InputBox>
                <p></p>
                {word_is_empty && <p></p>}
                <input type="text" ref={word_ref} placeholder=" "></input>
            </InputBox>

            <InputBox>
                <p></p>
                {mean_is_empty && <p></p>}
                <input type="text" ref={mean_ref} placeholder="  "></input>
            </InputBox>

            <InputBox>
                <p></p>
                {ex_is_empty && <p></p>}
                <input type="text" ref={ex_ref} placeholder=" "></input>
            </InputBox>

            <Button onClick={() => {

                if (word_ref.current.value === "") {
                    word_ref.current.focus();
                    setWord_is_empty(true);

                    return;

                } else if (mean_ref.current.value === "") {
                    mean_ref.current.focus();
                    setWord_is_empty(false);
                    setMean_is_empty(true);
                    return;

                } else if (ex_ref.current.value === "") {
                    ex_ref.current.focus();
                    setMean_is_empty(false);
                    setEx_is_empty(true);
                    return;
                }

                addWordList();
                history.goBack()
            }
            }></Button>

        </div>
    )
}

const InputBox = styled.div`
    background : white;
    padding : 10px;
    margin-bottom: 10px;

    & p{
        font-size: 15px;
        text-decoration: underline;
        margin: 0px;
        padding-bottom : 10px;
        
    }

    & input {
        width: 95%;
        height: 30px;
        margin-bottom: 5px;
    }

    & input:focus {
        outline: none !important;
        border: 2px #33D4AD solid ;
    }

`;

const Button = styled.button`
    
    width: 100%;
    height: 40px;
    color: white;
    font-size: 15px;
    font-weight: bold;
    background-color: #33D4AD;
    border : none;
    margin-top: 20px;
    cursor: pointer;
    

`;




export default Addword;



