import { useHistory } from "react-router-dom";
import { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import styled from "styled-components";
import icon from "../add_icon.png";




const WordList = () => {

    const history = useHistory();
    const word_lists = useSelector((state) => state.word.list);



    return (
        <div>
            <div>
                <h1 style={{ color: "#1FC69D" }}>MY DICTIONARY</h1>

                <ListStyle>
                    {word_lists.map((list, index) => {
                        return (
                            <WordStyle key={index}>
                                <p></p>
                                <h3>{list.word}</h3>
                                <p></p>
                                <h3>{list.mean}</h3>
                                <p></p>

                                <h3 className="ex">{list.ex}</h3>

                            </WordStyle>
                        )
                    })}
                </ListStyle>

            </div>

            <Button onClick={() => {
                history.push("/add");
            }}><img src={icon}></img></Button>
        </div>
    )
}

const ListStyle = styled.div`
    
    display: flex;
    flex-direction: column;
    height: 50vh;
    overflow-x: hidden;
    overflow-y: auto;
    max-height: 50vh;
    /* width : 100%; */

`;

const WordStyle = styled.div`
    background: white;
    margin-bottom : 10px;
    padding-left: 10px;
    width: 92%;
    word-break: break-all;
    

    & p{
        font-size: 15px;
        text-decoration: underline;
        margin: 10px 0px 0px 0px;
        padding : 0px;
        
    }
    & h3{
        margin-top:5px;
        padding-right : 10px;
    }
    & h3.ex{
        color: #33D4AD;
        font-size: 20px;
        font-weight: 300;
        font-style: italic;
        margin-bottom: 10px;
    }

`;

const Button = styled.button`
    border: none;
    background: none;
    

    & img{
        position: absolute;
        left:50%;
        top: 82%;
        margin-left:auto;
        margin-right: auto;
        width: 60px;
        cursor: pointer;
        transform: translate(-50%, -50%);
    }
`;

export default WordList;